*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
}
html{
  font-size: 62.5%;
  font-family: 'Spartan League', sans-serif;
  scroll-behavior: smooth;
  min-height: 100%;
}
body{
  width: 100%;
  overflow-x: hidden;
  background-color: #EAF4FE; 
  background-attachment: fixed;
}
#root{
  margin: 30px;
  position: relative;
  min-height: 100vh;
}
.loadingTodo__container {
    height: 45px;
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 10px;
  }
  
  .loadingTodo__container--text {
    margin: 24px 0 24px 44px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  
  .loadingTodo__container--check {
    border-radius: 50%;
    height: 28px;
    width: 28px;
  }

  .loadingTodo__container--delete{
    border-radius: 3px;
    height: 25px;
    width: 20px;
  }
  
  .loadingTodo__container--check {
    position: absolute;
    left: 12px;
  }

  .loadingTodo__container,
  .loadingTodo__container--check,
  .loadingTodo__container--delete{
    background: linear-gradient(90deg, rgba(250,250,250,1), rgb(200, 199, 199));
    background-size: 400% 400%;
    -webkit-animation: skeleton-loading 1.5s linear infinite alternate;
            animation: skeleton-loading 1.5s linear infinite alternate;
  }
  
  @-webkit-keyframes skeleton-loading {
    0% {
      background-color:hsl(200, 20%, 70%);

    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-color:hsl(200, 20%, 95%);
    }
  }
  
  @keyframes skeleton-loading {
    0% {
      background-color:hsl(200, 20%, 70%);

    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-color:hsl(200, 20%, 95%);
    }
  }
.error{
    font-size: 1.8rem;
    font-weight: 500;
    color: #0C1B32;
    text-align: center;
    margin-top: 20px;
}
.create{
    font-size: 1.8rem;
    font-weight: 500;
    color: #0C1B32;
    text-align: center;
    margin-top: 20px;
}
.date{
    font-size: 1.4rem;
    color: #676E7C;
    text-align: right;
    padding-bottom: 14px;
}
.title{
    font-size: 3rem;
    color: #1F314D;
    font-weight: bold;
    padding-bottom: 7px;
}
.motivation{
    font-size: 1.8rem;
    font-weight: 500;
    color: #676E7C;
    padding-bottom: 7px;
}
.todo__counter{
    font-size: 1.6rem;
    font-weight: 500;
    color: #0C1B32;
    padding-bottom: 20px;
}
.search__container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 30px;
    border: none;
    background-color: #BEBDBD;
    margin-bottom: 25px;
    border-radius: 25px;
    box-shadow: 0px 5px 5px rgba(32, 35, 41, 0.15);
}
.search__container--bar{
    width: 75%;
    height: 100%;
    border: none;
    background-color: transparent;
    outline: none;
    text-align: left;
    cursor: pointer;
    padding-top: 4px;
}
.search__container--icon{
    width: 20px;
    height: 20px;

}
.search__container--bar::-webkit-input-placeholder{
    color: #0C1B32;
    font-size: 1.6rem;
    font-weight: 500;
    margin: 0 auto;
}
.search__container--bar:-ms-input-placeholder{
    color: #0C1B32;
    font-size: 1.6rem;
    font-weight: 500;
    margin: 0 auto;
}
.search__container--bar::placeholder{
    color: #0C1B32;
    font-size: 1.6rem;
    font-weight: 500;
    margin: 0 auto;
}
.search__container--bar:focus::-webkit-input-placeholder{
    color: transparent;
}
.search__container--bar:focus:-ms-input-placeholder{
    color: transparent;
}
.search__container--bar:focus::placeholder{
    color: transparent;
}
.box{
    margin-top: 10px;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.box__check{
    color: black;
    width: 28px;
    height: 28px;
    margin-bottom: 8px;
    margin-right: 5px;
    cursor: pointer;
}

.box__todo{
    width: 258px;
    height: 45px;
    border-radius: 8px;
    background-color: #BEBDBD;
    margin-bottom: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.todo__text{
    font-size: 1.6rem;
    font-weight: 500;
    color: #0C1B32;
    margin-left: 15px;
}
.todo__delete{
    width: 20px;
    height: 25px;
    color: black;
    margin-right: 15px;
}
.todo__delete:hover{
    color: red;
    cursor: pointer;
}
.box__check--active{
    color: green;
    cursor: default;
}
.box__todo--active{
    background-color: #81C0FF;
}
.todo__text--active{
    text-decoration: line-through;
}
.todo__button{
    width: 60px;
    height: 60px;
    border: none;
    border-radius: 50%;
    background-color: #81C0FF;
    box-shadow: 0px 6px 25px rgba(83, 202, 235, 0.5);
    position: fixed;
    bottom: 30px;
    right: 30px;
    color: #FAFAFA;
    font-size: 5.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .4s ease;
    z-index: 10;
}
.todo__button:hover {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
.modal__background{
    background: rgba(63, 60, 60, 0.5);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}
.popup{
    max-width: 300px;
    width: 90%;
    max-height: 700px;
    background-color: #EAF4FE;
    border: 2px solid #BEBDBD;
    border-radius:10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.popup__message{
    font-size: 2.2rem;
    color: #1F314D;
    font-weight: bold;
    margin-top: 20px;
    margin-bottom: 20px;
}
.popup__input{
    width: 75%;
    height: 60px;
    font-size: 1.6rem;
    font-weight: 500;
    color: #1F314D;
    margin-bottom: 20px;
    line-height: 20px;
    text-align: center;
    box-shadow: 0px 6px 30px rgba(109, 109, 110, 0.3);
    border: 1px solid black;
    border-radius: 3px;
}
.popup__input:focus{
    outline-color: #2b1bb6;
}
.popup__input:focus::-webkit-input-placeholder{
    color: transparent;
}
.popup__input:focus:-ms-input-placeholder{
    color: transparent;
}
.popup__input:focus::placeholder{
    color: transparent;
}
.popup__buttons{
    width: 75%;
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 20px;
}
.popup__buttons--cancel{
    font-size: 1.6rem;
    color: #1F314D;
    width: 80px;
    height: 28px;
    border-radius: 3px;
    border: none;
    background-color: #BEBDBD;
}
.popup__buttons--submit{
    font-size: 1.6rem;
    color: #1F314D;
    width: 80px;
    height: 28px;
    border-radius: 3px;
    border: none;
    background-color: #81C0FF;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}
